.statistics {
  margin-top: 40px;
  padding-top: 20px;
  background-color: white;
}

.title {
  text-transform: uppercase;
}

.stat_list {
  display: flex;
  list-style: none;
  padding: 0;
  margin-bottom: 0;
}

.item {
  display: flex;
  list-style: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100px;
  padding: 20px 0;
}

.percentage {
  margin-top: 5px;
  font-weight: bold;
}
